import React, {Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle, CardActions, Button, CardMenu, IconButton, CardText} from 'react-mdl';
class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTab: 0
        }

    }
    toggleCategories() {
        if(this.state.activeTab === 0) {
            return (
                <div className = "projects-grid">
                <Card shadow = {5} style = {{minWidth: '450', margin: 'auto'}}>
                    <CardTitle style = {{color: 'black', height: '176px', background: 'url() center / cover' }} > 
                        React Project #1
                    </CardTitle>
                    <CardText>
                        <a href = "https://chrisyin98.github.io/File-Zipping-Website/">
                        Bied Biber
                            </a>
                    </CardText>
                    <CardActions border>
                        <Button> Github</Button>
                        <Button colored> Live Demo</Button>
                    </CardActions>
                    <CardMenu style = {{color: '#fff'}}>
                        <IconButton name = "share" colored />
                    </CardMenu>
                </Card>
                
                <Card shadow = {5} style = {{minWidth: '450', margin: 'auto'}}>
                    <CardTitle style = {{color: 'black', height: '176px', background: 'url() center / cover' }} > 
                        React Project #2
                    </CardTitle>
                    <CardText>
                        Ayeaowefij;aweofija;wefoi
                    </CardText>
                    <CardActions border>
                        <Button> Github</Button>
                        <Button colored> Live Demo</Button>
                    </CardActions>
                    <CardMenu style = {{color: '#fff'}}>
                        <IconButton name = "share" colored />
                    </CardMenu>
                </Card>
                
                </div>
            )
        } else if (this.state.activeTab === 1) {
            return (
                <div><h1> This is javascript </h1> </div> 
            )
        } else if (this.state.activeTab === 2) {
            return (
                <div> <h1> This is Java</h1></div>
            )
        }
    }

    
    
    render() {
        return( 
            <div className = "category-tabs">
                <Tabs activeTab = {this.state.activeTab} onChange = {(tabId) => this.setState({ activeTab: tabId})} ripple >
                    <Tab> React </Tab>
                    <Tab> Javascript </Tab>
                    <Tab> Java </Tab>

                </Tabs>

                    
                        <Grid >
                            <Cell col= {6}>
                                <div className = "content">
                                    {this.toggleCategories()}
                                </div>
                            </Cell>
                        </Grid>
                        
                    
            </div>
        )
    }
}

export default Projects;